



























































































import { FormModel } from '@/models/FormModel';
import Vue from 'vue';

export default Vue.extend({
  components: {
  },
  data() {
    return {
    };
  },
  computed: {
    memberForm(): FormModel {
      return this.$store.state.form;
    },
  },
  methods: {
    viewMemberInfo() {
      this.$router.push('viewMemberInfo');
      this.$store.commit('updateMemberInfo', this.memberForm.ceremonyForm);
    },
    editMemberInfo() {
      this.$router.push('ceremonyFormsPage/textname');
      this.$store.commit('updateMemberInfo', this.memberForm.ceremonyForm);
      this.$store.commit('updateSignature', '');
      this.$store.commit('updateConfirmApplication', true);
      this.$store.commit('updateCheckedRepeatList', true);
    },
    submitTime(time: number) {
      const date = new Date(time);
      return date.toLocaleDateString();
    },
  },
});
